import React, { useState } from 'react';
import showdown from './libraries/showdown';
import postsJson from './posts.json';

let wasFetched = false;

function App() {
  const [markdowns, setMarkdowns] = useState('<br/>');
  const converter = new showdown.Converter();
  const allMarkdowns = [];

  if(!wasFetched) {
    // this is to make sure that this fetch wil be called just once
    wasFetched = true;
    postsJson.forEach(({author, posts}) => {
      posts.forEach(({title, description}) => {
  
        fetch(`./posts/${author}/${title}.md`)
        .then(response => response.text())
        .then(md => {
          const h1 = `## ${title}`;
          allMarkdowns.push(converter.makeHtml(`${h1}\n${md}`));
          console.log(allMarkdowns);
          setMarkdowns(`
            <div class="post">${allMarkdowns.join('\n')}</div>
          `);
        });
      });
    });
  }

  return <div dangerouslySetInnerHTML={{__html: markdowns}}/>;
}

export default App;



